import { Component, OnInit, Input } from '@angular/core';
import { Exercise } from '../_models/exercise';
import { ExerciseService } from '../_services/exercise.service';

@Component({
  selector: 'app-exercise-list',
  templateUrl: './exercise-list.component.html',
  styleUrls: ['./exercise-list.component.css']
})
export class ExerciseListComponent implements OnInit {
  @Input() exercises: Exercise[];
  @Input() workoutMovementId: number;

  @Input() newExerciseReps: number;
  @Input() newExerciseWeight: number;

  constructor(private exerciseService: ExerciseService) { }

  ngOnInit() {
  }

  addExercise(weight: number, reps: number) {
    if (this.newExerciseReps != null && this.newExerciseWeight != null) {
      const exerciseToAdd = {
        weight: this.newExerciseReps,
        reps: this.newExerciseReps
      };

        // Get name of movement element selected and call
      this.exerciseService.createExercise(this.workoutMovementId, exerciseToAdd).subscribe(newExercise => {
        this.exercises.push(newExercise);
      });

      // this.exerciseService.createExercise(this.workoutMovementId, this.newExerciseWeight, this.newExerciseReps);
      this.newExerciseReps = null;
      this.newExerciseWeight = null;
    }
  }

  modifyExercise() {
    console.log('Modify exercise... hahah I wish it could do that.');
  }
}
