import { Component, OnInit, Input } from '@angular/core';
import { Movement } from '../_models/movement';
import { MovementService } from '../_services/movement.service';
import { Data } from '../data';

@Component({
  selector: 'app-movement-list',
  templateUrl: './movement-list.component.html',
  styleUrls: ['./movement-list.component.css']
})
export class MovementListComponent implements OnInit {
  @Input() movements: Movement[];
  @Input() workoutId: number;
  newMovement: string;
  movementOptions: string[];

  showMovementOptions: boolean;

  constructor(private movementService: MovementService) { }

  ngOnInit() {
    this.movementOptions = Data.movementList;
  }

  toggleAddMovement() {
    this.showMovementOptions = !this.showMovementOptions;
  }

  addMovement(movementToAdd: string) {
    console.log("Add movemnet - component " + this.workoutId + '  =   ' + movementToAdd);
    // Get name of movement element selected and call
    this.movementService.createMovement(this.workoutId, movementToAdd).subscribe(newMovement => {
      this.movements.push(newMovement);
    });

    this.showMovementOptions = !this.showMovementOptions;
  }
}
