import { Injectable } from '@angular/core';
import { Workout } from '../_models/workout';
import { HttpClient } from '@angular/common/http';
//import { Observable } from 'rxjs/Observable';

import { Data } from '../data';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class WorkoutService {
  constructor(private http: HttpClient) { }

  public getWorkouts(): Observable<Workout[]> {
    return this.http.get<Workout[]>('https://localhost:5001/api/Workout');
  }

  public getWorkout(id: number): Observable<Workout> {
    return this.http.get<Workout>('https://localhost:5001/api/Workout/' + id);
  }

  // Eventually update to pass user
  public createWorkout(): Observable<Workout> {
    return this.http.post<Workout>('https://localhost:5001/api/Workout', {
      'startDate': new Date(Date.now())
    });
  }

  updateWorkout(updatedWorkout: Workout): Observable<Workout> {
    return this.http.put<Workout>('https://localhost:5001/api/Workout/' + updatedWorkout.id, updatedWorkout);
  }

  removeWorkout(workoutToRemove: Workout) {
    this.http.delete('https://localhost:5001/api/Workout' + workoutToRemove.id);
  }
}
