import { Pipe, PipeTransform } from '@angular/core';
import { Exercise } from '../_models/exercise';
import { stringify } from 'querystring';

@Pipe({ name: 'stringifyExercise' })

export class StringifyExercise implements PipeTransform {
  transform(exercises: Exercise[]): String {
    let output = '';

    exercises.forEach(e => {
      let newExerciseString = '';
      if (e.repetitions != null) {
        newExerciseString += e.repetitions;
      }

      if (e.weight != null) {
        newExerciseString += ' @ ' + e.weight;
      }

      if (newExerciseString !== '') {
        output += newExerciseString + ', ';
      }
    });

    return output;
  }
}
