import { Component, OnInit } from '@angular/core';

import { Router, NavigationEnd } from '@angular/router';
@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  appTitle = 'Durkin Fitness';

  currentUrl: string; // Contains current URL (So we can highlight our current route link)
  // set class on a link to style it via [class.activated]="currentUrl == '/pathDoDah.."

  constructor(private router: Router) {
    router.events.subscribe((rt: NavigationEnd) => this.currentUrl = rt.url);
  }

  navbarOpen: boolean;
  toggleNavbar() {
    if (this.currentUrl !== '/') {
      this.navbarOpen = !this.navbarOpen;
    }
  }

  ngOnInit() {
    this.navbarOpen = false;
  }
}
